<template>
    
    <v-progress-linear 
    height="20"
    striped
    :color="color"
    :value="progress">
        <span :class="['caption']">
            {{ progress.toFixed(2) }}%
        </span>
    </v-progress-linear>
</template>
<script>


export default {
    name:'TableFieldSparkline',
    props: {
        field: String,
        value: Number,
        item: {
            type: Object,
        },
        header: {
            type: Object
        }
    },
    data: () => ({
        height: 10, 
        striped: true
    }),
    computed: {
        color () {
            const p = this.progress
            if ( p <= 15) return 'red'
            if ( p > 15 && p <= 30) return 'orange'
            if ( p > 30 && p <= 45) return 'orange lighten-1'
            if ( p > 45 && p <= 60) return 'yellow darken-1'
            if ( p > 60 && p <= 75) return 'green lighten-2'
            if ( p > 75 && p <= 90) return 'green lighten-1'
            return 'green'
        },
        progress () {
            const target = this.$getNestedValue(this.item, this.header.value)
            const value = this.$getNestedValue(this.item, this.field)
            return   value * 100 /  target  
        }
    } 
}
</script>

